body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  padding: 15px;
}

.nav-link.active {
  background-color: #4355fa;
  color: #FFF;
}

.td-check {
  width: 34px;
}
.check-all-link,
.check-all-link:hover {
  text-decoration: none;
  border-bottom: 1px dashed #666;
  color: #666;
}

.items-table {
  margin-top: 20px;
}

textarea {
  min-height: 100px;
}

.btn .spinner-border {
  margin-right: 5px;
}

.col-actions {
  text-align: right;
  font-size: 20px;
}
.col-actions a {
  margin-left: 12px;
}
.color-red {
  color: #D5583A;
}

.back-btn {
  margin-bottom: 15px;
}

.App {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}